import { StartEventModel,StartEventView  } from '@logicflow/extension';
import JqpUtil from './JqpUtil'
class JqpStartView extends StartEventView{

}
class JqpStartModel extends StartEventModel{
    getNodeStyle() {
        const style = super.getNodeStyle();
        const properties = this.properties;
        JqpUtil.getNodeStyle(style,properties);
        return style;
    }
}

export default {
    type:"JqpStart",
    view:JqpStartView,
    model:JqpStartModel
}
