import { ExclusiveGatewayModel,ExclusiveGatewayView  } from '@logicflow/extension';
import JqpUtil from './JqpUtil';
class JqpConditionView extends ExclusiveGatewayView{

}
class JqpConditionModel extends ExclusiveGatewayModel{
    getNodeStyle() {
        const style = super.getNodeStyle();
        const properties = this.properties;
        JqpUtil.getNodeStyle(style,properties);
        return style;
    }
}

export default {
    type:"JqpCondition",
    view:JqpConditionView,
    model:JqpConditionModel
}
