import { UserTaskView,UserTaskModel  } from '@logicflow/extension';
import JqpUtil from './JqpUtil';
class JqpUserTaskView extends UserTaskView{

}
class JqpUserTaskModel extends UserTaskModel{
    getNodeStyle() {
        const style = super.getNodeStyle();
        const properties = this.properties;
        JqpUtil.getNodeStyle(style,properties);
        return style;
    }
}

export default {
    type:"JqpUserTask",
    view:JqpUserTaskView,
    model:JqpUserTaskModel
}
