import { SequenceFlowView, SequenceFlowModel  } from '@logicflow/extension';
import JqpUtil from './JqpUtil'
class JqpSequenceFlowView extends SequenceFlowView{

}
class JqpSequenceFlowModel extends SequenceFlowModel{
    getEdgeStyle() {
        const style = super.getEdgeStyle();
        const properties = this.properties;
        JqpUtil.getNodeStyle(style,properties);
        return style;
    }
}

export default {
    type:"JqpSequenceFlow",
    view:JqpSequenceFlowView,
    model:JqpSequenceFlowModel
}
