import { EndEventModel,EndEventView  } from '@logicflow/extension';
import JqpUtil from './JqpUtil';
class JqpEndView extends EndEventView{

}
class JqpEndModel extends EndEventModel{
    getNodeStyle() {
        const style = super.getNodeStyle();
        const properties = this.properties;
        JqpUtil.getNodeStyle(style,properties);
        return style;
    }
}

export default {
    type:"JqpEnd",
    view:JqpEndView,
    model:JqpEndModel
}
