import { ServiceTaskView,ServiceTaskModel  } from '@logicflow/extension';
import JqpUtil from './JqpUtil'
class JqpSystemTaskView extends ServiceTaskView{

}
class JqpSystemTaskModel extends ServiceTaskModel{
    getNodeStyle() {
        const style = super.getNodeStyle();
        const properties = this.properties;
        JqpUtil.getNodeStyle(style,properties);
        return style;
    }
}

export default {
    type:"JqpSystemTask",
    view:JqpSystemTaskView,
    model:JqpSystemTaskModel
}
